.overlay {
    width: 100vw;
    height: 100vh;
    background: #0000009e;
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
}

.popup-container {
    width: 500px;
    height: 300px;
    background: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
