@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap');

* {
  font-family: 'Roboto', sans-serif;
}

.recipe-container {
  display: flex;
  width: 100%;
  justify-content: space-around;
  flex-wrap: wrap;
}

form {
  display: flex;
  flex-direction: column;
  padding: 50px;
  border-radius: 7px;
  box-shadow: 0 8px 12px 0 #c9c9c945;
  width: 20%;
  height: fit-content;
  background: white;
}

.vl {
  border-left: 1px solid #8080803b;
}

.recipe-list-container {
  width: 70%;
}

form p {
  font-size: 12px;
  color: grey;
  margin: 2px 0 8px;
}

input, textarea {
  border: 1px solid #7e0ae745;
  border-radius: 7px;
  padding: 10px;
}

.action-buttons {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

button, a.btn {
  border: none;
  background: radial-gradient(196.67% 196.67% at -54.22% -111.25%, #FDC04C 0%, #E47029 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
  padding: 10px 20px;
  font-weight: 400;
  font-size: 18px;
  color: white;
  border-radius: 7px;
  cursor: pointer;
  margin: 7px 10px 0;
}

button:hover {
  background: #FFFFFF;
  box-shadow: 1px 2px 10px #e4702952;
  color: #E47029;
}

.recipe-list-item {
  padding: 20px;
  border-radius: 7px;
  box-shadow: 0 5px 12px 0 #c2c2c245;
  margin-bottom: 12px;
  background: white;
}

.recipe-list-title {
  display: flex;
  height: 50px;
  margin: 10px 0;
  width: 100%;
  align-items: center;
}

@media(max-width: 1200px) {
  .vl {
    display: none;
  }

  form {
    width: 100%;
  }
}

.recipe-title, .ingredient-list, .instruction-list {
  border-bottom: 1px solid #e8eae8;
  padding-bottom: 10px;
}

h4, ul, ol {
  margin: 5px 0;
}
