* {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

html {
  width: 100vw;
  height: 100vh;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  background: #e9e9ff24;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav {
  height: 10%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 20px;
  box-shadow: 0 5px 12px 0 #c9c9c924;
  background: white;
}

nav a {
  margin: 10px;
  text-decoration: none;
  color: indigo;
  cursor: pointer;
  padding: 10px 20px;
}

nav a:hover {
  background: indigo;
  color: white;
  border-radius: 7px;
}

iframe#main-container {
  border: none;
  width: 100%;
  height: 80%;
  padding: 30px;
}
